import React, { useState, useEffect } from 'react';

import Usercard from './Usercard'

// material ui
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";



const useStyles = makeStyles((theme) => ({
  grid: {
    flexGrow: 1
  }
}));




function Cardlist(props){


      // Similar to componentDidMount and componentDidUpdate:
    useEffect(() => {
      // Update the document title using the browser API
      console.log('useeffect list');
    });

    
    console.log('CardListprops', props.JSONdataArray);
    const {JSONdataArray} = props;
    const classes = useStyles();

    console.log('JSONdataArray.length',JSONdataArray.length);
    return(<>
       <Grid justify="center" container className={classes.grid}>
           { 
               JSONdataArray.map((JSONdataArrayToCard, index)=>{
                return (<div key={JSONdataArrayToCard.login.uuid} >
                  <Grid  item xs>
                      <Usercard key={'usercard-' + JSONdataArrayToCard.login.uuid} {...JSONdataArrayToCard}/>
                  </Grid>
                  </div>)
               })
           }
       </Grid>
    </>) 
};

export default Cardlist;



